'use strict';module.exports = {"en":{"01.about-this":"Forget about networks. SUGOS enables you to call remote functions as if they are there.\n\n\n","02.how-it-works":"SUGOS is consisting of three parts.\n\n+ [SUGO-Hub][sugo_hub_url] - Http server which works as a hub for callers and actors.\n+ [SUGO-Actor][sugo_actor_url] - Http client to receive commands and kick functions  \n+ [SUGO-Caller][sugo_caller_url] - Http client to send procedure call to remote actor\n\n<img src='images/sugos-overview.png' />\n\nOnce you declare a function on an actor, the hub server shares it's signature with callers.\nThen, the callers dynamically define functions to wrap remote procedure calling.     \n\nThus, function like\n\n```javascript\nfunction sayHelloTo(name){ /* ... */}\n``` \n\non the actor-side cloud be called as\n\n```javascript\nsayHelloTo(\"Liz\")\n```\n\nfrom the caller-side.\n\n\n[sugo_hub_url]: https://github.com/realglobe-Inc/sugo-hub \n[sugo_actor_url]: https://github.com/realglobe-Inc/sugo-actor \n[sugo_caller_url]: https://github.com/realglobe-Inc/sugo-client \n","03.what-to-use":"With SUGOS, you can build IoT applications or or Cloud-Robotics quite easily.\n\nSee [use cases](./cases.html) to know what we do with SUGOS.\n \nIn addition to that, we have [more live examples](https://github.com/realglobe-Inc/sugos#more-examples) which you can deploy to your heroku.   \n\n\n","10.quick-start":"","11.setup-hub":"Setup a [SUGO-Hub](https://github.com/realglobe-Inc/sugo-hub) server for actors and callers.","12.declare-on-sugo-actor":"Create a [SUGO-Actor](https://github.com/realglobe-Inc/sugo-actor) instance and declare modules. Then, connect to the cloud server.","13.call-from-sugo-caller":"Create a [SUGO-Caller](https://github.com/realglobe-Inc/sugo-caller) instance and connect to the actor with key.\nThen get access to modules and call functions as you like.\n","20.case-plen":"SUGOS make it super easy to controlling robot remotely.\n\nFor example, you can control [PLEN][plen_url] robot from web browser.\n\nThe way it works is:\n\n1. Tapping button on browser kicks SUGO-Caller methods\n2. SUGO-Caller send packets to remote SUGO-Actor via SUGO-Hub\n3. SUGO-Actor received a command and drive the robot connected \n\n[plen_url]: https://plen.jp/\n","21.case-drone":"Once you setup modules on actor, you can easily customize logic to call then on caller side.\n\nFor example, define preset for [ArDrone][ardrone_url] about how it flies. A simple Javascript on browser would do this. \n  \n[ardrone_url]: http://www.parrot.com/usa/products/ardrone-2/\n","22.case-kinect":"\nNot only sending commands. SUGOS supports event-emit interface, which enables you to receive events from remote actor.\n   \nThis is an example to detect human body frame with [Kinect Sensor][kinect_url] and display the result on remote browser. \n\n[kinect_url]: https://msdn.microsoft.com/en-us/library/hh438998.aspx\n","23.case-pepper-voice":"Sinces SUGO-Callers run on browsers, you can easliy combine web browser feature like [Web Speech API][html5_speech_api_url].\n\nFor example, detect speech with browser and make pepper to talk it.\n\n[html5_speech_api_url]: https://developer.mozilla.org/en-US/docs/Web/API/Web_Speech_API\n","24.case-pepper-text":"Or, just create a text-box and make the pepper to talk it.","25.roomba-edison":"With some little handy work, SUGOS makes even more fun!\n \nThis is an example to control [Roomba vacuuming robot][roomba_url] via [Intel Edison Board][edison_url]\n\nA SUGO-Actor instance runs on the Edison and send commands to Roomba serial port.\n\n  \n\n[roomba_url]: http://www.irobot.com/For-the-Home/Vacuuming/Roomba.aspx\n[edison_url]: http://www.intel.com/content/www/us/en/do-it-yourself/edison.html","26.edison-webcom":"SUGOS event interface enables you to send data from SUGO-Actor to SUGO-Caller.\n   \nFor example, run a SUGO-Actor instance on Edison Board and send video stream data from a webcom. \n","27.curl-rapiro":"Sometimes you want to send command via simple HTTP request, without SUGO-Callers.\n\nLittle trick with hub servers would do this. \nSUGO-Hub is based on [Koa][koa_url] framework, and you can add your own middleware on it.\n\n \n[koa_url]: https://github.com/koajs/koa\n","28.hitoe-map":"Mashing up device and web API is one of the most powerful use cases for SUGOS.\n \nFor example, use [Hitoe sensor][hitoe_url] to detect sudden illness and display location of the patient on Google Map\n\n[hitoe_url]: https://www.ntt-review.jp/archive/ntttechnical.php?contents=ntr201409ra1.html","29.iphone-pepper":"A single SUGO-Caller may connect multiple SUGO-Actors.\n\nThis example shows detecting motion from iOS and sending it to pepper robot.","actor":"# sugo-actor@4.4.2\n\nActor component of SUGOS.\n\n+ Functions\n  + [sugoActor(config)](#sugo-actor-function-sugo-actor)\n+ [SugoActor](sugo-actor-classes) Class\n  + [new SugoActor(url, config)](#sugo-actor-classes-sugo-actor-constructor)\n  + [actor.connect()](#sugo-actor-classes-sugo-actor-connect)\n  + [actor.disconnect()](#sugo-actor-classes-sugo-actor-disconnect)\n  + [actor.perform(data)](#sugo-actor-classes-sugo-actor-perform)\n  + [actor.load(moduleName, module)](#sugo-actor-classes-sugo-actor-load)\n  + [actor.loadSub(moduleName, subModules)](#sugo-actor-classes-sugo-actor-loadSub)\n  + [actor.unload(name)](#sugo-actor-classes-sugo-actor-unload)\n\n## Functions\n\n<a name=\"sugo-actor-function-sugo-actor\" ></a>\n\n### sugoActor(config) -> `SugoActor`\n\nCreate an actor instance. Just an alias of `new SugoActor(config)`\n\n| Param | Type | Description |\n| ----- | --- | -------- |\n| config | Object | Sugo caller configuration |\n\n```javascript\nco(function * () {\n  let actor = sugoActor({\n    key: 'my-actor-01',\n    modules: {\n    }\n  })\n  yield actor.connect()\n}).catch((err) => console.error(err))\n```\n\n\n<a name=\"sugo-actor-classes\"></a>\n\n## SugoActor Class\n\n\n\n\n<a name=\"sugo-actor-classes-sugo-actor-constructor\" ></a>\n\n### new SugoActor(url, config)\n\nConstructor of SugoActor class\n\n| Param | Type | Description |\n| ----- | --- | -------- |\n| url | string | Cloud server url |\n| config | object | Configurations |\n| config.key | string | Key of actor |\n| config.auth | object | Auth object |\n| config.modules | object.&lt;String, SugoActorModule&gt; | Modules to load. |\n\n\n<a name=\"sugo-actor-classes-sugo-actor-connect\" ></a>\n\n### actor.connect() -> `Promise`\n\nConnect to hub.\nBy call this, actor share specification of the modules to hub so that callers can access them.\n\n<a name=\"sugo-actor-classes-sugo-actor-disconnect\" ></a>\n\n### actor.disconnect() -> `Promise`\n\nDisconnect from the hub\n\n<a name=\"sugo-actor-classes-sugo-actor-perform\" ></a>\n\n### actor.perform(data) -> `Promise`\n\nHandle perform event\n\n| Param | Type | Description |\n| ----- | --- | -------- |\n| data | object |  |\n\n\n<a name=\"sugo-actor-classes-sugo-actor-load\" ></a>\n\n### actor.load(moduleName, module) -> `Promise`\n\nLoad a module\n\n| Param | Type | Description |\n| ----- | --- | -------- |\n| moduleName | string | Name of module |\n| module | Object | Module to load |\n\n\n<a name=\"sugo-actor-classes-sugo-actor-loadSub\" ></a>\n\n### actor.loadSub(moduleName, subModules) -> `Promise`\n\nLoad submodules\n\n| Param | Type | Description |\n| ----- | --- | -------- |\n| moduleName | string |  |\n| subModules | Object |  |\n\n\n<a name=\"sugo-actor-classes-sugo-actor-unload\" ></a>\n\n### actor.unload(name) -> `Promise`\n\nUnload module with name\n\n| Param | Type | Description |\n| ----- | --- | -------- |\n| name | string | Name of module |\n\n\n\n\n","caller":"# sugo-caller@3.0.3\n\nCaller component of SUGOS.\n\n+ Functions\n  + [sugoCaller(config)](#sugo-caller-function-sugo-caller)\n+ [SugoCaller](sugo-caller-classes) Class\n  + [new SugoCaller(config)](#sugo-caller-classes-sugo-caller-constructor)\n  + [caller.connect(key)](#sugo-caller-classes-sugo-caller-connect)\n  + [caller.disconnect(key)](#sugo-caller-classes-sugo-caller-disconnect)\n+ [ActorAccessBundle](sugo-caller-classes) Class\n  + [new ActorAccessBundle()](#sugo-caller-classes-actor-access-bundle-constructor)\n  + [bundle.get(moduleName, options)](#sugo-caller-classes-actor-access-bundle-get)\n  + [bundle.has(moduleName)](#sugo-caller-classes-actor-access-bundle-has)\n  + [bundle.set(moduleName, module, options)](#sugo-caller-classes-actor-access-bundle-set)\n  + [bundle.del(moduleName)](#sugo-caller-classes-actor-access-bundle-del)\n  + [bundle.names()](#sugo-caller-classes-actor-access-bundle-names)\n\n## Functions\n\n<a name=\"sugo-caller-function-sugo-caller\" ></a>\n\n### sugoCaller(config) -> `SugoCaller`\n\nCreate a caller instance. Just an alias of `new SugoCaller(config)`\n\n| Param | Type | Description |\n| ----- | --- | -------- |\n| config | Object | Sugo caller configuration |\n\n```javascript\nco(function * () {\n  let caller = sugoCaller({})\n  let actor01 = yield caller.connect('my-actor-01')\n  let foo = actor01.get('foo') // Get a module of actor\n  yield foo.sayYeah() // Call the remote function\n}).catch((err) => console.error(err))\n```\n\n\n<a name=\"sugo-caller-classes\"></a>\n\n## SugoCaller Class\n\nHub client for caller side.\nWhen you connect to remote actor with a caller, it receives specification of the actor and dynamically define function kick actor side function.\nThis way you can magically call functions declared on remote as if they were here.\n\n\n<a name=\"sugo-caller-classes-sugo-caller-constructor\" ></a>\n\n### new SugoCaller(config)\n\nConstructor of SugoCaller class\n\n| Param | Type | Description |\n| ----- | --- | -------- |\n| config | Object | Caller configuration |\n| config.protocol | string | Protocol to use ( \"http\" or \"https\" ) |\n| config.host | string | Hub host name. ( eg: \"localhost:3000\" ) |\n| config.pathname | string | Hub URL path name ( eg: \"/callers\" ) |\n| config.auth | Object | Auth data for hub |\n\n\n<a name=\"sugo-caller-classes-sugo-caller-connect\" ></a>\n\n### caller.connect(key) -> `Promise.<ActorAccessBundle>`\n\nConnect to actor\n\n| Param | Type | Description |\n| ----- | --- | -------- |\n| key | string | Key of actor |\n\n\n<a name=\"sugo-caller-classes-sugo-caller-disconnect\" ></a>\n\n### caller.disconnect(key) -> `Promise`\n\nDisconnect from cloud server\n\n| Param | Type | Description |\n| ----- | --- | -------- |\n| key | string | Key of actor to connect |\n\n\n<a name=\"sugo-caller-classes\"></a>\n\n## ActorAccessBundle Class\n\nBundle for actor access.\nThis class provides access for loaded modules on actor.\n\n\n<a name=\"sugo-caller-classes-actor-access-bundle-constructor\" ></a>\n\n### new ActorAccessBundle()\n\nConstructor of ActorAccessBundle class\n\n\n\n<a name=\"sugo-caller-classes-actor-access-bundle-get\" ></a>\n\n### bundle.get(moduleName, options) -> `ActorAccessModule`\n\nGet a module\n\n| Param | Type | Description |\n| ----- | --- | -------- |\n| moduleName | string | Name of module |\n| options | Object | Optional settings |\n\n\n<a name=\"sugo-caller-classes-actor-access-bundle-has\" ></a>\n\n### bundle.has(moduleName) -> `Boolean`\n\nCheck if module exists\n\n| Param | Type | Description |\n| ----- | --- | -------- |\n| moduleName | string | Name of module |\n\n\n<a name=\"sugo-caller-classes-actor-access-bundle-set\" ></a>\n\n### bundle.set(moduleName, module, options)\n\nSet module\n\n| Param | Type | Description |\n| ----- | --- | -------- |\n| moduleName | string | Name of module |\n| module | ActorAccessModule | Module to set |\n| options | Object | Optional settings |\n\n\n<a name=\"sugo-caller-classes-actor-access-bundle-del\" ></a>\n\n### bundle.del(moduleName)\n\nDelete module\n\n| Param | Type | Description |\n| ----- | --- | -------- |\n| moduleName | string | Name of module |\n\n\n<a name=\"sugo-caller-classes-actor-access-bundle-names\" ></a>\n\n### bundle.names() -> `Array.<string>`\n\nGet names of modules\n\n\n\n","hub":"# sugo-hub@4.0.1\n\nHub server of SUGOS\n\n+ Functions\n  + [sugoHub()](#sugo-hub-function-sugo-hub)\n+ [SugoHub](sugo-hub-classes) Class\n  + [new SugoHub(options)](#sugo-hub-classes-sugo-hub-constructor)\n\n## Functions\n\n<a name=\"sugo-hub-function-sugo-hub\" ></a>\n\n### sugoHub() -> `Promise.<SugoHub>`\n\nCreate a hub instance. Just an alias of `new SugoCaller(config)`\n```javascript\nco(function * () {\n  let cloud = sugoHub({\n  // Options here\n  })\n  yield hub.listen(3000)\n}).catch((err) => console.error(err))\n```\n\n\n<a name=\"sugo-hub-classes\"></a>\n\n## SugoHub Class\n\nHub server of SUGOS\n\n\n<a name=\"sugo-hub-classes-sugo-hub-constructor\" ></a>\n\n### new SugoHub(options)\n\nConstructor of SugoHub class\n\n| Param | Type | Description |\n| ----- | --- | -------- |\n| options | Object | Optional settings |\n| options.storage | string,Object | Storage options |\n| config.keys | string | Koa keys |\n| options.endpoints | Object | Endpoint settings |\n| config.context | Object | Koa context prototype |\n| config.public | string | Public directories. |\n| options.invalidateInterval | number | Interval for invalidate loop |\n\n\n\n\n","tip-module-variations":"## Ways to Declare Actor Modules\n\nThere are several ways to declare a module for actor\n\n### new Module(methods)\n\nThe most basic way is passing object represents methods\n\n```javascript\nconst sugoActor = require('sugo-actor')\nconst {Module} = sugoActor\n\nlet module01 = new Module({\n  doSomething () { /* ... */ }\n})\n\n```\n\n### new Module(func)\n \nMethods could be a single functions. \n\n```javascript\nlet module02 = new Module(function asFunc () { /* ... */ })\n```\n\nThe module it self will be a function on caller side.\n\nFor more detail, see https://github.com/realglobe-Inc/sugo-actor#declare-a-single-function-as-module\n\n\n### new (class Custom {} extends Module)()\n\nActor accepts instances of custom class which extends `Module`. \n\n```javascript\nclass CustomModule extends Module {\n  doSomething () { /* ... */ }\n}\nlet module03 = new CustomModule()\n```\n\nFor more detail, see https://github.com/realglobe-Inc/sugo-module-base#define-custom-class\n\n\n### new (Module.modularize(SomeClass))()\n\nIf you want use existing class which is not a sub class of `Module`, make it actor-compatible with `Module.modularize()` utility.   \n\n```javascript\nclass MyClass {\n  doSomething () { /* ... */ }\n}\n\nconst MyClassModularized = Module.modularize(MyClass)\n \nlet module04 = new MyClassModularized()\n```\n\nFor more detail, see https://github.com/realglobe-Inc/sugo-module-base#modularize-existing-class"},"ja":{}};